.about-us {
  @include pageGrid;

  &__title {
    margin-bottom: 32px;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__description {
    margin: 0;
    font-size: 23px;
    line-height: 140%;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
      margin-bottom: 12px;
    }

    @include onDesktop {
      grid-column: span 6;
      margin-bottom: 20px;
    }
  }
}
